// Related links with images
.related-links--with-images {
    a {
        margin-left: $measure--half;
        font-weight: $font-weight-normal;

        &:after {
            display: none;
            content: none;
        }
    }

    &.background--gray2,
    &.background--purewhite {
        span:before {
            color: $color-inline-link;
            font-size: 1rem;
        }

        a {
            @include anchor-inline($color-inline-link, $color-inline-link);
        }
    }

    &.background--purewhite {
        background-color: $color-purewhite;
    }

    &.background--pink80 {
        background-color: $color-pink80;
    }

    &.background--red70 {
        background-color: $color-red70;
    }

    &.background--pink80,
    &.background--red70 {
        h2 {
            color: $color-purewhite;
        }

        span:before {
            color: $color-gray4;
            font-size: 1rem;
        }

        a {
            @include anchor-inline($color-gray4, $color-gray4);
        }
    }

    .pathway-image-cards__blocks {
        display: block;
        p a {
            display: inline-block;
        }
    }
}
