.dropdown-filter {
    li[data-hidden='true'] {
        display: none;
    }

    select {
        @include font-family('default');
        @include body-font-features();
        @include alphatype();

        background-color: transparent;
        border: $border-width-default $border-style-solid $color-pink90;
        bottom: $measure--quarter;
        color: $color-nearblack;
        font-weight: $font-weight-medium;
        height: $measure--medium4;
        position: relative;
        width: 100%;

        &:focus {
            @include outline-properties();
        }
    }
}
