.js .cookie-notice {
    display: none;

    p {
        max-width: 55ch;
        //margin: 0 auto;
        padding: $measure--small2;
    }

    a {
        @include anchor-inline(
            $color-gray12,
            $color-gray12,
            $color-purewhite,
            $color-outline--dark
        );

        transition: color 0.3s ease;
    }

    &[data-display='true'] {
        @include media('screen') {
            display: flex;
            align-items: center;
            justify-content: center;
            @include extrasmalltype();
            background: rgba($color-gray11, 0.95);
            bottom: 0;
            color: $color-text--dark;
            left: 0;
            position: fixed;
            width: 100vw;
            z-index: 'inpage';
        }
    }

    button {
        @include cta-primary-arrow-dark-bg();
    }

    @include media('<tablet') {
        flex-direction: column;

        button {
            margin-bottom: $measure--small2;
        }
    }
}
