.modal--in.no-scroll {
    position: fixed;
    width: 100%;

    .modal__background {
        opacity: 0.7;
    }
}

.modal__background {
    background: $color-nearblack;
    pointer-events: none;
    height: 100%;
    opacity: 0;
    top: 0;
    left: 0;
    position: fixed;
    transition: opacity 0.35s ease;
    width: 100%;
    z-index: z-index('veil');
}

.js {
    .modal__popup {
        align-items: center;
        display: flex;
        height: 100%;
        justify-content: center;
        left: 0;
        opacity: 1;
        pointer-events: auto;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: z-index('modal');
    }
}

.modal__popup[data-hidden='true'] {
    opacity: 0;
    pointer-events: none;
}

.modal__content {
    background: $color-purewhite;
    border: none;
    overflow-y: auto;
    padding: $measure--small2 $measure--medium1 $measure--medium1;
    position: relative;
}

.modal__heading {
    @include standfirst;
    font-size: $font-size--gamma;
    font-weight: $font-weight-medium;
}

// .modal_strapline {
//     color: $color-red50;
// }

.modal__close {
    color: $color-gray7;
    padding: $measure--threequarters;
    position: absolute;
    right: 1px;
    top: 1px;

    @include anchor-default-outline($color-outline);

    &:hover {
        @include media('hover') {
            background-image: none;
            border: none;
            background-color: $color-gray1;
            color: $color-gray10;
        }

        &:before {
            font-size: $font-size--icon + 0.2;
        }
    }

    &:active {
        background-color: $color-gray1;
        color: $color-gray10;
    }

    &:focus {
        color: $color-gray10;
    }

    &:active,
    &:focus {
        outline-offset: 0;
    }
}

.modal__body-copy {
    margin-top: $measure--basic;
}

.modal__buttons--calendar {
    a {
        @include anchor-reset-properties;
        @include anchor-default-outline($color-outline);
        border: $border-width-hairline $border-style-solid $color-gray3;
        border-bottom-color: $color-gray3;
        border-bottom-width: $border-width-hairline;
        color: $color-gray8;
        display: block;
        font-size: $font-size--alpha;
        margin-top: $measure--small4;
        padding: $measure--threequarters;
        position: relative;

        &:visited {
            border-bottom-color: $color-gray3;
            color: $color-gray8;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                color: $color-purewhite;
                border: $border-width-hairline $border-style-solid $color-pink90;
                border-bottom-color: $color-pink90;
                border-bottom-width: $border-width-hairline;
                background-color: $color-pink90;
                background-image: none;
                background-size: initial;
            }
        }

        &:focus {
            background-color: $color-gray1;
            border: $border-width-hairline $border-style-solid $color-gray4;
            border-bottom-color: $color-gray4;
            border-bottom-width: $border-width-hairline;
            color: $color-gray11;
        }

        &:active {
            background-color: $color-pink100;
            border: $border-width-hairline $border-style-solid $color-pink100;
            border-bottom-color: $color-pink100;
            border-bottom-width: $border-width-hairline;
            color: $color-purewhite;
        }
    }
}

.modal__buttons--slider {
    margin-top: $measure--medium1;

    button {
        @include anchor-default-outline();
        @include smalltype;
        padding: $measure--basic;
        align-items: center;
        width: 50%;

        @include media('>=tablet') {
            display: flex;
            position: relative;
        }

        &:first-child {
            position: relative;
            text-align: left;
            padding-left: 0;

            @include media('>=tablet') {
                padding-left: $measure--small2;
            }

            &:after {
                content: '';
                height: 100%;
                width: $border-width-default;
                background: $color-red50;
                position: absolute;
                top: 0;
                right: 0;
            }

            .modal__buttons__icon {
                @include media('>=tablet') {
                    margin-right: $measure--threequarters;
                    left: 0;
                }
            }
        }

        &:last-child {
            text-align: right;
            padding-right: 0;
            justify-content: flex-end;

            @include media('>=tablet') {
                padding-right: $measure--small2;
            }

            .modal__buttons__icon {
                @include media('>=tablet') {
                    margin-left: $measure--threequarters;
                    right: 0;
                }
            }
        }

        &:hover {
            .modal__underline-transition {
                span {
                    background-image: linear-gradient(
                        $color-red50,
                        $color-red50
                    );
                    background-size: 100% $border-width-default;
                }
            }
        }

        .modal__underline-transition {
            @include underline-transition;

            span {
                position: relative;
                background-image: linear-gradient(
                    $color-nearblack,
                    $color-nearblack
                );
                background-position: 0% 100%;
                background-repeat: no-repeat;
                background-size: 0% $border-width-default;
                transition: background-size $transition-duration--long ease-in;
                padding-bottom: $border-width-default;
            }
        }
    }
}

.modal__buttons__icon {
    margin-top: $measure--threequarters;

    @include media('>=tablet') {
        margin-top: 0;
        position: absolute;
    }
}

.modal__buttons {
    max-width: $max-line-length;

    @include media('>=tablet') {
        display: flex;
    }
}

.js .modal__body {
    display: none;
}
