.wrapper--featured-profiles__profile {
    justify-content: flex-start;

    .featured-profiles__profile {
        display: flex;
        padding: 0 $measure--basic $measure--basic 0;
        margin: $measure--basic $measure--basic $measure--basic 0;
        transition: $transition-duration--long;
    }
}

@mixin featured-profiles-block(
    $text-color,
    $link-color,
    $link-color-engaged,
    $profile-bg-color,
    $profile-bg-color-engaged,
    $profile-link-color-engaged
) {
    color: $color-gray4;
    a {
        color: $link-color;
        background-image: none;
        border-bottom: $link-color $border-style-solid $border-width-default;
    }

    .linked-heading {
        grid-column: 2/-2;
        border-bottom: 0;
        &:focus {
            outline-color: $link-color-engaged;
            outline-offset: $measure--half;
        }
        .linked-heading__text {
            color: $text-color;
            border-bottom-color: $text-color;
        }
    }

    .wrapper--featured-profiles__heading-intro {
        color: $text-color;
    }

    .wrapper--featured-profiles__profile {
        .featured-profiles__profile {
            background: $profile-bg-color;
            &:focus {
                outline-color: $profile-link-color-engaged;
            }
            &:hover,
            &:focus-within {
                background: $profile-bg-color-engaged;
                .featured-profiles__profile__label {
                    h3 a {
                        color: $profile-link-color-engaged;
                        border-bottom-color: $profile-link-color-engaged;
                    }
                    p {
                        color: $profile-link-color-engaged;
                    }
                    .fa-long-arrow-right {
                        visibility: visible;
                        color: $color-red50;
                    }
                }
            }
            .featured-profiles__profile__image {
                width: $measure--large2;
                img {
                    height: $measure--medium11;
                    object-fit: cover;
                }
            }
            .featured-profiles__profile__label {
                margin-left: $measure--basic;
                h3 {
                    margin-top: 0;
                    a {
                        color: $link-color;
                        border-bottom-color: $link-color;
                        background-image: none;
                        border-bottom: $link-color $border-style-solid
                            $border-width-default;
                        &:hover {
                            background-image: none;
                        }
                        &:focus {
                            outline-color: $profile-link-color-engaged;
                        }
                    }
                }
                p {
                    color: $text-color;
                }
                .fa-long-arrow-right {
                    visibility: hidden;
                    font-size: $font-size--epsilon;
                    font-weight: $font-weight-light;
                    margin-top: $measure--quarter;
                }
            }
        }
    }
}

.featured-profiles__heading,
.wrapper--featured-profiles__heading-intro {
    grid-column: 1/-1;
    @include media('>mobile') {
        grid-column: 2/-2;
    }
}

// Layout variation -> Row
.featured-profiles-block--row {
    @include content-grid();
    grid-column: 1/-1;
    margin: 0;
    padding: $measure--basic 0 $measure--small4;

    .wrapper--featured-profiles__profile {
        grid-column: 1/-1;
        display: grid;
        grid-column-gap: $measure--basic;
        grid-template-columns: repeat(1, 1fr);
        @include media('>mobile') {
            grid-column: 2/-2;
        }
        @include media('>tablet') {
            grid-template-columns: repeat(2, 1fr);
        }
        @include media('>desktop') {
            display: flex;
            flex-wrap: wrap;
            .featured-profiles__profile {
                width: calc(1 / 3 * 100% - (1 - 1 / 3) *#{$measure--basic});
            }
        }
    }
}

// Layout variation -> Grid
.featured-profiles-block--grid {
    .wrapper--featured-profiles__heading-intro {
        grid-column: 1/-1;
    }
    .wrapper--featured-profiles__profile {
        grid-column: 1/-1;
        display: grid;
        grid-column-gap: $measure--basic;
        grid-template-columns: repeat(1, 1fr);
        @include media('>mobile') {
            grid-column: 2/-2;
        }
        @include media('>tablet') {
            grid-template-columns: repeat(2, 1fr);
        }
        @include media('>desktop') {
            grid-column: 8/-2;
            display: grid;
            grid-column-gap: 1.5rem;
            grid-template-columns: repeat(10, 1fr);
            .featured-profiles__profile {
                &:nth-of-type(odd) {
                    grid-column: 1/6;
                }
                &:nth-of-type(even) {
                    grid-column: 6/-1;
                }
            }
        }
    }
}
/*
$text-color,
$link-color,
$link-color-engaged,
$profile-bg-color,
$profile-bg-color-engaged,
$profile-link-color-engaged
*/

.featured-profiles-block {
    &.background--purple80 {
        @include featured-profiles-block(
            $color-purewhite,
            $color-gray4,
            $color-gray4,
            $color-purple90,
            $color-purewhite,
            $color-gray10
        );
    }
    &.background--gray9 {
        @include featured-profiles-block(
            $color-purewhite,
            $color-gray4,
            $color-gray4,
            $color-gray10,
            $color-purewhite,
            $color-gray10
        );
    }
    &.background--gray2 {
        @include featured-profiles-block(
            $color-nearblack,
            $color-nearblack,
            $color-nearblack,
            $color-purewhite,
            $color-pink90,
            $color-purewhite
        );
    }
    &.background--gray1 {
        @include featured-profiles-block(
            $color-nearblack,
            $color-nearblack,
            $color-nearblack,
            $color-purewhite,
            $color-pink90,
            $color-purewhite
        );
    }
    &.background--purewhite {
        @include featured-profiles-block(
            $color-gray10,
            $color-gray10,
            $color-purple90,
            $color-gray2,
            $color-pink90,
            $color-purewhite
        );
    }
}
