// Desktop specific layout if secondary navigation is present
main {
    @include media('>=small') {
        > .course--ug {
            .promo-box__wrapper {
                @include x-column-grid(18);

                .promo-box {
                    grid-column: 3/17;
                }
            }
            .course--ug__keyinfo__wrapper {
                @include x-column-grid(18);

                .course--ug__keyinfo {
                    grid-column: 3/17;
                }
            }
        }
    }
}

// tablet / mobile layout
main {
    @include media('<small') {
        > .course--ug {
            .course--ug__keyinfo__wrapper {
                .course--ug__keyinfo {
                    @include standard-box;
                    margin-top: $measure--small2;
                    margin-bottom: $measure--small2;
                }
            }

            .promo-box__wrapper {
                @include standard-box;
                display: block;
            }
        }
    }
}

// Generic styles
.course--ug {
    .course--promo-cards {
        @include standard-grid-wrapper();
    }
    .course--ug__student-testimonials__wrapper {
        @include media('<=small') {
            @include standard-box;
        }

        .course--ug__student-testimonials {
            @include standard-grid;

            @include media('<=small') {
                grid-column: 1 / -1;
            }

            @include media('<=tablet') {
                grid-column-gap: $measure--basic;
            }

            .course--ug__student-testimonials__half {
                grid-column: span 12;

                @include media('<=desktop', '>small') {
                    grid-column: 1 / -1;
                    margin-bottom: $measure--basic;
                }

                @include media('<tablet') {
                    grid-column: 1 / -1;
                    margin-bottom: $measure--basic;
                }
            }
        }
    }

    .course-info__wrapper {
        margin-bottom: $measure--small4;
    }

    .course-info__year {
        justify-content: space-between;

        @include media('<=mobile') {
            display: block;
        }

        .start-date {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @include gammatype;

            @include media('<=between') {
                margin-bottom: $measure--basic;
            }

            .next-year {
                text-align: center;
            }
        }
    }

    .promo-box__wrapper {
        padding: 0;
        display: grid;
    }

    .promo-box__container {
        display: flex;

        @include media('<=between') {
            flex-direction: column;
        }
    }

    .course-info__cta {
        a:first-child {
            margin-bottom: $measure--basic;
        }
    }

    .course--promo-cards__ranking--first,
    .course--promo-cards__ranking--sec {
        @include media('<=mobile') {
            grid-column: 1/-1;
        }

        @include media('>mobile', '<=between') {
            grid-column: 1/13;
        }
    }

    .course--ug__keyinfo__intro,
    .course--ug__keyinfo__details,
    .course--ug__keyinfo__accordion {
        background: linear-gradient(
            360deg,
            rgba(255, 255, 255, 0.95) 0%,
            rgba(255, 255, 255, 0.95) 100%
        );
        padding: $measure--small3 $measure--small3;

        @include media('<=mobile') {
            grid-column: 1 / -1;
        }

        @include media('>mobile') {
            grid-column: 2 / 12;
        }

        @include media('>between') {
            grid-column: 3 / 17;
        }
    }

    .course--ug__keyinfo__intro {
        margin-top: $measure--small3;
    }

    .course--ug__keyinfo__details {
        margin-top: $measure--small3;
        display: flex;
        flex-wrap: wrap;

        &--column {
            flex: 1;

            @include media('<between') {
                flex-basis: 50%;

                &:nth-child(3) {
                    margin-top: $measure--small3;
                }
            }

            @include media('<tablet') {
                flex-basis: 100%;

                &:nth-child(2) {
                    margin-top: $measure--small3;
                }
            }
        }

        .label {
            display: block;
            @include font-family('heading');
            color: $color-gray7;
            font-weight: $font-weight-normal;
            margin-bottom: $measure--half;
        }

        span:not(.label) {
            display: block;
            color: $color-gray9;
            font-weight: $font-weight-book;
            @include font-family('default');
            margin-bottom: $measure--quarter;
        }
    }

    .course--ug__keyinfo__wrapper {
        margin-top: $measure--small4;
        display: grid;
    }

    .course--ug__keyinfo__details__ucas-code {
        margin-top: $measure--small3;
    }

    .course--ug__keyinfo__details__location {
        margin-top: $measure--small3;
    }

    .parallax {
        background-attachment: fixed;
        background-size: cover;
    }

    .course--ug__keyinfo__accordion {
        border-top: $border-width-default $border-style-solid $color-gray3;
        margin-bottom: $measure--small3;

        .accordion {
            .accordion__heading {
                background: $color-gray1;

                button {
                    color: $color-nearblack;
                    font-weight: $font-weight-book;
                    @include alphatype();
                }
            }
        }
    }

    .course--ug__section-wrapper--grey {
        background-color: $color-gray1;
    }

    .course--ug__usp {
        grid-column: 3 / 24;

        .standfirst {
            @include epsilontype();
            color: $color-pink80;
        }
    }

    .course--ug__usp__cta,
    .course--ug__usp__stats {
        background-color: $color-purewhite;

        li {
            display: inline-block;
            list-style-type: none;
            margin-right: $measure--half;

            &:last-of-type {
                margin-right: 0;
            }
        }
    }

    .featured-profiles-block {
        @include media('<=mobile') {
            @include default-padding();
        }
    }

    .course--ug__related-courses__wrapper {
        background: linear-gradient(
            360deg,
            rgba(255, 255, 255, 1) 0%,
            rgba(242, 242, 242, 1) 100%
        );
    }

    .course--ug__related-courses {
        @include standard-box();
        padding-bottom: $measure--medium8;
        padding-top: $measure--small2;

        .related-content {
            display: grid;
            margin-top: $measure--basic;
            grid-template-columns: auto auto;

            @include media('<between') {
                display: block;
            }
        }

        h2 {
            @include heading--centered($color-pink80);
        }
    }

    .course--ug__pathway-image-cards {
        display: grid;
        grid-column-gap: $measure--medium7;
        grid-row-gap: $measure--medium7;
        margin-top: $measure--small4;

        @include media('>=tablet') {
            grid-template-columns: auto auto;
        }
    }

    .accordion {
        @include media('<tablet') {
            .show-more__text[data-hidden='true']:after {
                background-image: linear-gradient(
                    to bottom,
                    rgba(255, 255, 255, 0),
                    rgba(242, 242, 242, 1) 90%
                );
            }
        }
    }

    .chart-row {
        margin: 0;
        padding: 0;

        @include media('>=tablet') {
            display: flex;
        }
    }

    .chart__columns {
        @include media('<tablet') {
            display: flex;

            .chart--piechart,
            .chart__legend {
                flex: 1;
            }

            .chart--piechart {
                margin-right: $measure--basic;
                svg {
                    width: $measure--medium11;
                }
            }

            .chart__legend {
                margin-top: 0;
            }
        }
        @include media('>=tablet') {
            display: block;
        }
    }
}

// tabs styling
.js {
    .course--ug {
        .tabs {
            @include standard-box;
            background: $color-purewhite;
            margin-top: $measure--medium7;

            .tabs__panel {
                border-top: none;
                background: transparent;
            }

            .tabs__panel__heading {
                text-align: center;
            }

            .tabs__panel__body {
                margin-top: $measure--medium7;
            }
        }
    }

    .accordion > .accordion__heading {
        border-bottom-color: transparent;
    }
}

.course--ug__section-wrapper--grey {
    background-color: $color-gray1;
}

.course--ug__usp {
    grid-column: 3 / 24;

    .standfirst {
        @include epsilontype();
        color: $color-pink80;
    }
}

.course--ug__usp__cta {
    background-color: $color-purewhite;
    display: block;
    padding: $measure--quarter 0 $measure--basic;

    @include media('>tablet') {
        display: flex;
        justify-content: space-around;
    }

    p {
        display: block;

        @include media('>tablet') {
            display: inline-block;
            margin-right: $measure--half;
        }

        &:last-of-type {
            margin-right: 0;
        }
    }
}

.course--ug__usp__stats {
    background-color: $color-purewhite;
}

.course--ug__usp__video {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: $measure--basic;

        .embedded-video {
            padding: 0;
        }
    }
}

.course--ug__student-coursework,
.course--ug__facilities {
    @include standard-box;
    @include media('>tablet') {
        margin: 0;
        padding: 0;
    }
}

.course--ug__facilities__video {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-column-gap: $measure--basic;
    }
}

.course--ug__student-coursework__videos {
    @include media('>tablet') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: $measure--basic;
    }
}

.statistics-block--ug {
    .ksp__items--ug {
        display: flex;
        justify-content: space-between;
    }

    .ksp__item--ug {
        flex: 1;
        margin-right: $measure--small2;
    }

    .ksp__item__figure {
        font-weight: $font-weight-light;
        color: $color-red50;
    }

    .ksp__item__overline {
        @include font-family('default');
        font-weight: $font-weight-book;
        font-size: $font-size--alpha;
        color: $color-gray7;
    }
}

.course__text_bg {
    span {
        background: $color-purewhite;
        display: inline-block;
        &:first-of-type {
            width: 8ch;
            margin-left: $border-width-default;
        }
    }
}

.ug-text-table {
    margin-bottom: $border-width-default;
    display: flex;

    .ug-text-table__prop,
    .ug-text-table__value {
        background: $color-purewhite;
        padding: 0 $border-width-default;
    }

    .ug-text-table__prop {
        margin: 0 $border-width-default;
        flex-basis: 15%;
        span {
            @include content-style();
            font-weight: $font-weight-bold;
        }
    }

    .ug-text-table__value {
        flex-grow: 1;
        span {
            @include content-style();
        }
    }
}

.ug-career__testimonial {
    background-color: $color-gray1;
}

.courses--ug__facilities,
.course--ug__student-coursework,
.course--ug__student-testimonials__wrapper {
    h2 {
        @include full-width-heading();
    }
}

.courses--ug__facilities {
    @include standard-box;
    @include media('>tablet') {
        margin: 0;
        padding: 0;
    }
}

.course--ug__student-testimonials__half {
    grid-column: 1/-1;

    @include media('>tablet') {
        grid-column: span 12;
    }
}

.ug-career__testimonial {
    padding: $measure--basic;
}

.course--ug__usp__stats {
    .statistics-block {
        .ksp__items {
            text-align: center;
            img {
                margin: auto;
            }
        }

        .ksp__item__figure {
            color: $color-pink80;
        }

        .ksp__items.ksp__items--3 {
            @include media('<tablet') {
                grid-template-rows: repeat(3, auto);
                grid-template-columns: repeat(6, 1fr);
                grid-template-areas:
                    '. a a a a .'
                    '. b b b b .'
                    '. c c c c .';
            }
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);
                grid-template-areas:
                    '. a a a a a b b b b b .'
                    '. . . c c c c c c . . .';
            }

            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
                grid-template-areas: '. a a a a a b b b b b b c c c c c .';
            }
        }

        .ksp__items.ksp__items--2 {
            @include media('<tablet') {
                grid-template-rows: repeat(3, auto);
                grid-template-columns: repeat(6, 1fr);
                grid-template-areas:
                    '. a a a a .'
                    '. b b b b .';
            }
            @include media('>=tablet', '<=desktop') {
                grid-template-rows: repeat(2, auto);
                grid-template-columns: repeat(12, 1fr);
                grid-template-areas: '. a a a a a b b b b b .';
            }

            @include media('>desktop') {
                grid-template-columns: repeat(18, 1fr);
                grid-template-areas: '. . a a a a a a b b b b b b b . .';
            }
        }

        .ksp__item {
            &:nth-child(1) {
                grid-area: a;
            }
            &:nth-child(2) {
                grid-area: b;
            }
            &:nth-child(3) {
                grid-area: c;
            }
        }
    }
}

.course--ug__student-testimonials__half--unibuddy {
    h3 {
        color: $color-red50;
    }
}

@include media('screen') {
    .js .ourse--ug .tabs__links {
        @include default-margin();
    }

    .js .course--ug .accordion {
        @include media('>tablet') {
            @include standard-box();
        }
    }

    .js .course--ug .tabs {
        @include media('<tablet') {
            @include display-tabs-content();
        }
    }

    .js main {
        > .course--ug {
            .tabs__panel:not([hidden]) {
                padding: 0;
                .tabs__panel__content {
                    @include standard-grid();

                    grid-template-rows: repeat(2, auto);

                    @include media('<=mobile') {
                        @include default-padding();
                    }

                    .tabs__panel__heading {
                        grid-row: 1;
                    }

                    .tabs__panel__body {
                        grid-row: 2;
                    }

                    .tabs__panel__heading,
                    .tabs__panel__body {
                        grid-column: 1 / span 24;

                        @include media('>mobile', '<=between') {
                            grid-column: 5 / span 16;
                        }

                        @include media('>between') {
                            grid-column: 7 / span 12;
                        }
                    }
                }

                .tabs .tabs {
                    .tabs__panel:not([hidden]) {
                        padding: $measure--small1 0;
                    }

                    margin: 0;
                    padding: 0;

                    .tabs__panel__content {
                        display: block;
                        padding: 0 $measure--basic;
                    }

                    .tabs__panel__body {
                        margin-top: $measure--small1;
                    }
                }
            }
        }

        > .navigation--secondary + .course--ug {
            .tabs__panel:not([hidden]) {
                padding: $measure--small1 0;

                > .tabs__panel__content {
                    @include media('>=small') {
                        @include content-grid();

                        .tabs__panel__heading,
                        .tabs__panel__body {
                            grid-column: 4 / span 12;
                        }
                    }
                }
            }

            .tabs .tabs {
                margin: 0;
                padding: 0;

                .tabs__panel:not([hidden]) {
                    padding: $measure--small1 0;
                }

                .tabs__panel__content {
                    display: block;
                    padding: 0 $measure--basic;
                }

                .tabs__panel__body {
                    margin-top: $measure--small1;
                }
            }
        }
    }
}

.course--ug__tabs__panel__inner--during {
    grid-column: 2/-1;
}

.unibuddy-card {
    margin-top: $measure--medium3;
}
