.no-js {
    .how-to-apply--pgt--no-js {
        display: block;
        margin-bottom: $measure--small2;
    }

    .tabs .how-to-apply--pgt--js {
        display: none;
    }
}

.js {
    .how-to-apply--pgt--no-js {
        display: none;
    }

    .tabs .how-to-apply--pgt--js {
        display: block;
    }
}
