.research-degree {
    .highlighted-border--left {
        padding: $measure--quarter $measure--basic;
        margin: $measure--half 0;
        border-left: $measure--quarter $border-style-solid $color-purple70;
        .research-degree__fees {
            display: flex;
            p {
                padding-right: $measure--basic;
                flex-basis: 50%;
                span {
                    display: block;
                    &.research-degree__fees__value {
                        color: $color-purple70;
                        font-weight: $font-weight-medium;
                    }
                }
            }
        }
    }
    .icon-wrapper {
        margin: 0 $measure--basic 0 0;
        display: table;
        line-height: $measure--small3;
        padding-bottom: #{$measure--quarter + ($measure--quarter / 2)};
        .icon {
            display: table-cell;
            padding: $measure--quarter $measure--half 0 0;
            margin: $measure--quarter $measure--quarter 0 0;
            color: $color-purple70;
        }
    }
}

.research-degree .banner-text.banner--center {
    background: $color-gray9;
}

.research-degree__standfirst {
    max-width: 100%;
    grid-column: 1/-1;
    margin: $measure--medium1 0;
    @include media('>tablet') {
        grid-column: 2/-2;
    }
    @include media('>between') {
        grid-column: 3/-3;
    }
    @include media('>desktop') {
        grid-column: 5/15;
    }

    .standfirst {
        color: $color-purewhite;
    }
}

.wrapper--research-degree__pre-overview {
    @include media('>mobile') {
        padding-bottom: $measure--huge5;
    }
}

.research-degree__org-units-research-centres {
    grid-column: 1/-1;
    margin: $measure--small5 0 $measure--quarter 0;
    @include media('>tablet') {
        grid-column: 2/12;
    }
    @include media('>between') {
        grid-column: 5/15;
    }

    .research-degree__org-units-research-centres__org-units__label {
        &:after {
            content: '\2219';
            margin: 0 $measure--half;
        }
        &:last-child {
            &:after {
                display: none;
            }
        }
    }

    ul {
        @include media('>between') {
            columns: 2;
        }
        margin-left: 0;
        max-width: 100%;

        li {
            list-style-type: none;
        }
    }
}

.key-information--research {
    grid-column: 1/-1;
    margin: $measure--quarter 0 $measure--small5 0;
    @include media('>tablet') {
        grid-column: 2/12;
    }
    @include media('>between') {
        grid-column: 5/15;
    }
    .key-information--research__main {
        margin-top: $measure--basic;
    }
}

.wrapper--research-degree__promo-box {
    .promo-content {
        grid-column: 1/-1;
        margin: 0 0 $measure--medium3 0;
        @include media('>tablet') {
            grid-column: 2/12;
        }
        @include media('>between') {
            grid-column: 4/16;
        }
    }
}

.research-degree__overview {
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    @include media('>mobile') {
        grid-template-columns: repeat(12, 1fr);
    }
    grid-column-gap: $measure--small2;
    padding-top: $measure--small2;
    margin-bottom: $measure--medium7;

    @include media('>mobile') {
        margin-top: -$measure--huge1;
    }
    @include media('>tablet') {
        grid-column: 2/12;
    }
    @include media('>between') {
        grid-column: 4/16;
    }

    .show-more {
        @include media('<tablet') {
            margin: 0 $measure--small2;
        }
        h2 {
            @include heading-centered();
        }
    }

    .research-degree__overview__content {
        grid-column: 1/-1;
        @include media('>tablet') {
            grid-column: 2/12;
        }
        @include media('>between') {
            grid-column: 3/11;
        }
    }
    .research-degree__overview__content .accordion {
        grid-column: 1/-1;
    }
}

.research-degree__overview__content {
    .research-degree__overview__content__apply-links {
        display: flex;
        .modal-group {
            flex-basis: 50%;
            list-style-type: none;
            margin: 0 $measure--basic 0 0;

            li {
                a {
                    display: block;
                }
            }

            .modal__buttons {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                margin-top: $measure--basic;

                a {
                    flex-basis: 48%; // Add small gap between buttons inside modal
                    margin-top: $measure--half;
                }
            }
        }
    }
}

.wrapper--research-degree__research-projects {
    .research-degree__research-projects {
        grid-column: 2/-2;
        h2.research-degree__research-projects__heading {
            margin-top: $measure--medium1;
        }
    }
}

.wrapper--research-degree__testimonials {
    margin-top: $measure--medium7;
    .research-degree__testimonials__content {
        display: block;
        .related-content.slider {
            @include media('<=mobile') {
                display: block;
            }
            @include media('>mobile') {
                grid-column: 1/-1;
            }
            .slider__active-slide {
                grid-column: 1/-1;
                @include media('>tablet') {
                    grid-column: 2/-2;
                }
                @include media('>between') {
                    grid-column: 3/14;
                }
            }
            .slider__controls__wrap {
                margin: $measure--basic 0;
                grid-column: 7/-1;
            }
        }
    }
}

.wrapper--research-degree__footer {
    padding: $measure--small4 0;

    .research-degree__footer__useful-links {
        margin-top: $measure--small4;
    }
    .research-degree__footer__contact {
        max-width: none;
        margin: 0;
    }
    .research-degree__footer__contact--one-card {
        .research-degree__footer__contact--one-column {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 1/7;
            }
        }
        .research-degree__footer__useful-links {
            grid-column: 1/-1;
            @include media('>tablet') {
                grid-column: 7/13;
            }
        }
    }
    // Flexible styles for either 1 or 2 contact cards
    .research-degree__footer__contact--two-cards {
        .research-degree__footer__contact--two-column {
            display: grid;
            grid-template-columns: repeat(12, 1fr);
            grid-column-gap: $measure--small2;
            grid-column: 1/-1;
            @include media('>between') {
                grid-column: 1/13;
            }
            .card {
                grid-column: 1/-1;
                &:nth-of-type(1) {
                    @include media('>tablet') {
                        grid-column: 1/7;
                    }
                }
                &:nth-of-type(2) {
                    @include media('>tablet') {
                        grid-column: 7/-1;
                    }
                }
            }
        }
        .research-degree__footer__useful-links {
            grid-column: 1/-1;
            @include media('>tablet', '<=between') {
                grid-column: 1/7;
            }
            @include media('>between') {
                grid-column: 13/-1;
            }
        }
    }
}
.research-degree__footer__useful-links {
    h3 {
        @include betatype;
        font-weight: $font-weight-medium;
        margin-top: 0;
        color: $color-gray7;
        border-bottom: $border-width-hairline $border-style-solid $color-gray2;
        padding-bottom: $measure--half;
    }
}
