    background: $color-gray2;
    grid-template-rows: auto;

    > .content {
        grid-area: main;
        margin: 0;
        display: grid;
        grid-template-rows: auto;

        @include media('<=mobile') {
            grid-template-columns: repeat(6, 1fr);
            grid-template-areas:
                ' vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap '
                ' careers careers careers careers careers careers '
                ' pathway pathway pathway pathway pathway pathway '
                ' outcomes outcomes outcomes outcomes outcomes outcomes '
                ' gallery gallery gallery gallery gallery gallery '
                ' testimonial testimonial testimonial testimonial testimonial testimonial '
                ' news-events news-events news-events news-events news-events news-events ';
        }

        @include media('>mobile') {
            grid-template-columns: repeat(12, 1fr);
            grid-template-areas:
                ' vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap '
                ' careers careers careers careers careers careers careers careers careers careers careers careers '
                ' pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway '
                ' outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes '
                ' gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery '
                ' testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial '
                ' news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events ';
        }

        @include media('>between') {
            @include standard-grid();
            grid-template-areas:
                ' vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap vf-wrap'
                ' . careers careers careers careers careers careers careers careers careers careers careers careers careers careers careers careers .'
                ' . pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway . '
                ' . outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes outcomes . '
                ' . gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery gallery '
                ' . testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial testimonial . '
                ' news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events news-events ';
        }
    }
}

.content-hub__block-two .grey-rectangle--careers-section {
    background: $color-gray2;
    height: 64rem;
    left: 9rem;
    margin-bottom: -51.875rem;
    margin-top: -30.875rem;
    position: relative;
    top: -18.5rem;
    transform: rotate(60deg);
    width: 60rem;
    z-index: z-index('below');

    // Disable SVG on mobile
    @include media('<=large') {
        display: none;
    }
    // Safari fix -> stop SVG extending beyond window width
    @supports (overflow: -webkit-marquee) and (justify-content: inherit) {
        width: 100%;
    }
}

.content-hub__block-one .banner {
    grid-column: 1/-1;
}

.content-hub__block-one .section {
    margin: $measure--small2;
}

.content-hub .banner__standfirst {
    h1 {
        text-align: center;
    }

    .standfirst {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
    }
}

.content-hub__block-one .video-finder-wrapper {
    grid-area: vf-wrap;
    grid-column: 1/-1;
    display: grid;
    grid-template-columns: repeat(18, 1fr);
    grid-column-gap: $measure--small2;
    grid-template-rows: auto;
    background-color: $color-gray2;
    margin-top: 0;
    margin-bottom: -$measure--large2;
    @include media('<=mobile') {
        grid-template-columns: repeat(6, 1fr);
        grid-template-areas:
            'video video video video video video'
            'finder finder finder finder finder finder';
    }
    @include media('>mobile') {
        grid-template-columns: repeat(12, 1fr);
        grid-template-areas:
            'video video video video video video video video video video video video'
            'finder finder finder finder finder finder finder finder finder finder finder finder';
    }
    @include media('>between') {
        grid-template-columns: repeat(18, 1fr);
        grid-template-areas:
            ' . . . . video video video video video video video video video video . . . . '
            ' . . . finder finder finder finder finder finder finder finder finder finder finder finder . . . ';
    }

    @include media('>=desktop') {
        .embedded-video {
            padding-top: 0;
        }
    }
}

.content-hub__block-one .embedded-video {
    grid-area: video;
    padding-bottom: 0;
}

.content-hub__block-one .embedded-video .youtube-video {
    padding-left: 0;
}

.content-hub__block-one .explore {
    grid-column: 1/-1;
    background: $color-purewhite;
    margin-top: $measure--medium3;
    padding: $measure--small4 $measure--huge3 $measure--medium3;

    @include media('<=desktop') {
        padding: $measure--small4 $measure--medium3 $measure--small4;
    }

    @include media('<=large') {
        margin-top: 0;
        border: $border-width-hairline $border-style-solid $color-gray2;
    }
    h2.explore__heading {
        text-align: center;
        margin: 0;
    }
}

.content-hub__block-two {
    margin: $measure--large2 0 $measure--basic;
}

.content-hub__block-two .careers {
    grid-area: careers;
    padding: $measure--basic 0 0;
    @include media('<between') {
        padding: $measure--basic 0 $measure--small4;
    }
    @include standard-box();
    @include standard-grid();

    // Set up internal careers section grid
    grid-column-gap: $measure--small2;
    grid-template-rows: auto;

    @include media('<=mobile') {
        grid-template-areas:
            'careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro'
            'pathway pathway pathway pathway pathway pathway'
            'job-title job-title job-title job-title job-title job-title'
            'employer employer employer employer employer employer';
    }
    @include media('>mobile') {
        grid-template-areas:
            'careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro '
            'pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway'
            'job-title job-title job-title job-title job-title job-title employer employer employer employer employer employer';
    }
    @include media('>between') {
        grid-template-areas:
            '. . . careers-intro careers-intro careers-intro careers-intro careers-intro careers-intro . . . . . . . . . '
            '. . . pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway pathway . . . '
            'job-title job-title job-title job-title job-title job-title job-title job-title job-title employer employer employer employer employer employer employer employer employer ';
    }
}

.content-hub__block-two .careers__intro {
    grid-area: careers-intro;
}

.content-hub__block-two .careers__job-titles {
    grid-area: job-title;
}

.content-hub__block-two .careers__job-titles ul {
    columns: 2;
    @include media('<=between') {
        columns: 1;
    }
}

.content-hub__block-two .careers__employers {
    grid-area: employer;
}

.content-hub__block-two .careers__employer-logos-wrapper img {
    margin: $measure--basic $measure--half 0 0;
    max-height: $measure--medium7;
    padding: $measure--half;
    border: $border-width-hairline $border-style-solid $color-gray2;
}

.content-hub__block-two .careers__job-locations h2 {
    grid-area: a;
}

.content-hub__block-two .pathway-cards {
    --pathway-offset: 50%;
    --pathway-offset-half: calc(var(--pathway-offset) / 2);
    --negative-offset-tiny: calc(var(--pathway-offset) / -10);
    --negative-offset-small: calc(var(--pathway-offset) / -2);
    --negative-offset-large: calc(var(--negative-offset-small) * 2);

    @include media('>between') {
        margin-top: $measure--large4;
    }

    @include media('<=between') {
        margin-top: $measure--small4;
    }

    grid-area: pathway;

    .card--pathway__block {
        display: grid;
        grid-template-rows: auto;
        grid-column-gap: $measure--half;

        @include media('<=mobile') {
            grid-template-areas:
                'pathway1 pathway1 pathway1 pathway1 pathway1 pathway1 '
                'pathway2 pathway2 pathway2 pathway2 pathway2 pathway2 '
                'pathway3 pathway3 pathway3 pathway3 pathway3 pathway3 '
                'pathway4 pathway4 pathway4 pathway4 pathway4 pathway4 '
                'pathway5 pathway5 pathway5 pathway5 pathway5 pathway5 '
                'pathway6 pathway6 pathway6 pathway6 pathway6 pathway6 ';
        }

        @include media('>mobile') {
            grid-template-areas:
                'pathway1 pathway1 pathway1 pathway1  pathway1 pathway1 pathway2 pathway2 pathway2 pathway2 pathway2 pathway2'
                'pathway3 pathway3 pathway3 pathway3 pathway3 pathway3 pathway4 pathway4 pathway4 pathway4 pathway4 pathway4'
                'pathway5 pathway5 pathway5 pathway5 pathway5 pathway5 pathway6 pathway6 pathway6 pathway6 pathway6 pathway6';
        }

        @include media('>between') {
            grid-template-columns: repeat(12, 1fr);
            grid-column-gap: $measure--small2;
            grid-template-areas:
                'pathway1 pathway1 pathway1 pathway1 pathway2 pathway2 pathway2 pathway2 pathway3 pathway3 pathway3 pathway3'
                'pathway4 pathway4 pathway4 pathway4 pathway5 pathway5 pathway5 pathway5 pathway6 pathway6 pathway6 pathway6';
        }
    }
}

.content-hub__block-two .pathway-cards .card {
    @include media('>between') {
        // Create enough space so, regardless of content length, card rows don't overlap
        margin-bottom: var(--pathway-offset);
    }
    a {
        @include pathway-card-background($color-red50);
        &:hover {
            @include media('hover') {
                .card__inner p {
                    color: $color-purewhite;
                }
            }
        }
    }

    @include media('>mobile') {
        padding-right: $measure--basic;
    }

    // Create 'step' effect on cards up to two rows
    &:nth-of-type(1) {
        grid-area: pathway1;
    }
    &:nth-of-type(2) {
        grid-area: pathway2;
        @include media('>between') {
            margin-top: var(--negative-offset-small);
        }
    }
    &:nth-of-type(3) {
        grid-area: pathway3;
        @include media('>between') {
            margin-top: var(--negative-offset-large);
        }
    }
    &:nth-of-type(4) {
        grid-area: pathway4;
    }
    &:nth-of-type(5) {
        grid-area: pathway5;
        @include media('>between') {
            margin-top: var(--negative-offset-small);
        }
    }
    &:nth-of-type(6) {
        grid-area: pathway6;
        @include media('>between') {
            margin-top: var(--negative-offset-large);
        }
    }
}

.content-hub__block-two .outcomes {
    grid-area: outcomes;
    @include standard-box();
    margin: $measure--basic $measure--small2 $measure--medium7;
}

.content-hub__block-two .outcomes h2:nth-of-type(1) {
    text-align: center;
}

.content-hub__block-two .swiper-viewport {
    padding: $measure--basic $measure--small2;
    grid-area: gallery;
}

.content-hub__block-two .gallery .section-title {
    text-align: center;
}

.content-hub__block-two .testimonial {
    grid-area: testimonial;
    @include standard-box();
}

.content-hub__block-two .testimonial .slider {
    display: grid;
}

.content-hub__block-two .testimonial .slider li {
    grid-column: 1/-1;
}

.content-hub__block-two .testimonial .cta-block {
    max-width: none;
    @include standard-grid();
}

.content-hub__block-two .testimonial .cta-block li.further-info {
    grid-column: 1/-2;
    @include media('<=between') {
        grid-column: 1/-1;
    }
    text-align: right;
    a {
        @include cta-basic-light-bg($color-gray2, $color-gray11);
        box-shadow: none;
    }
}

.content-hub--news-events .news-events .related-content {
