            'research research research research research research '
            'happening happening happening happening happening happening';
    }

    @include media('>tablet') {
        grid-template-columns: repeat(12, 1fr);
        grid-template-areas:
            'separator separator separator separator separator separator separator separator separator separator separator separator'
            '. . . research research research research research research . . . '
