        @include anchor-discreet();
    }
}

.content-hub__block-three .news-events .content-hub__block-three__research {
    display: grid;
    margin-bottom: $measure--medium7;
    @include media('<=between') {
        margin-bottom: $measure--basic;
    }
    @include media('<=tablet') {
        grid-column: 1/-1;
    }
    @include media('>tablet') {
        grid-column: 4/10;
    }
    @include media('>between') {
        grid-column: 1/-1;
    }
}

.content-hub__block-three
    .news-events
    .content-hub__block-three__research__centres__title {
    text-align: left;
}

.content-hub__block-three
    .news-events
    .content-hub__block-three__research
    .related-content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 1.5rem;

    @include media('<=tablet') {
        grid-column: 1/-1;
    }
    @include media('<=between') {
        grid-column: 1/-1;
        margin-bottom: $measure--medium3;
    }
    @include media('>between') {
        grid-column: 7/-1;
    }

    @include media('<=between') {
        li {
            grid-column: 1/-1;
        }
    }

    @include media('>between') {
        li:nth-of-type(1) {
            grid-column: 1/7;
        }
        li:nth-of-type(2) {
            grid-column: 7/-1;
        }
    }
}

.content-hub__block-three
    .news-events
    .content-hub__block-three__happening
    h2.section-title,
.content-hub__block-three
    .news-events
    .content-hub__block-three__happening
    h2.related-content__heading {
    text-align: center;
    margin-bottom: $measure--basic;
}

/**
Adjust grid layout when only case study(ies) exists in research section, i.e. no research studies.
This will keep responsive behaviour in line with What's Happening cards below
*/
.content-hub__block-three
    .news-events
    .content-hub__block-three__research.case-study-only {
    grid-column: 1/-1;
    .related-content {
        @include standard-grid();
        grid-column: 1/-1;
    }
}
